.page {
    width: 100%;
}

.page-title {
    font-size: 1.3rem;
    font-weight: bold;
    color: $night;
    padding: 10px 0;
    margin-bottom: 10px;
}

.page-enter {
    opacity: 0;
    transform: scale(1.1);
}

.page-enter-active {
    opacity: 1;
    transform: scale(1);
    transition: opacity 300ms, transform 300ms;
}

.page-exit {
    opacity: 1;
    transform: scale(1);
}

.page-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 300ms, transform 300ms;
}

